import validators from '../validators';

export default () => {
    return {
        key: {
            validators: [validators.string]
        },
        /**
         * Sets or returns the value of the `countryCode` option.
         *
         * Represents the country's code.
         *
         * @attribute countryCode
         * @param {String} [countryCode] 2 or 3 country code letters.
         * @throws {TypeError} If the given argument cannot be converted into a valid value.
         */
        countryCode: {
            validators: [validators.countryCode],
            required: true
        },
        limit: {
            validators: [validators.limit]
        },
        offset: {
            validator: [validators.naturalInteger],
            name: 'ofs'
        },
        language: {
            validators: [validators.languageCode]
        },

        /**
         * Sets or returns the value of the `streetNumber` option.
         *
         * Represents the street's number for the structured address.
         *
         * @attribute streetNumber
         * @param {String} [streetNumber] Street's number.
         * @throws {TypeError} If the given argument cannot be converted into a valid value.
         */
        streetNumber: {
            validators: [validators.string]
        },

        /**
         * Sets or returns the value of the `streetName` option.
         *
         * Represents the street's name for the structured address.
         *
         * @attribute streetName
         * @param {String} [streetName] Street's name.
         * @throws {TypeError} If the given argument cannot be converted into a valid value.
         */
        streetName: {
            validators: [validators.string]
        },

        /**
         * Sets or returns the value of the `crossStreet` option.
         *
         * Represents the cross street's name for the structured address.
         *
         * @attribute crossStreet
         * @param {String} [crossStreet] Cross street's name.
         * @throws {TypeError} If the given argument cannot be converted into a valid value.
         */
        crossStreet: {
            validators: [validators.string]
        },

        /**
         * Sets or returns the value of the `municipality` option.
         *
         * Represents the municipality (city/town) for the structured address.
         *
         * @attribute municipality
         * @param {String} [municipality] Municipality's name (city/town).
         * @throws {TypeError} If the given argument cannot be converted into a valid value.
         */
        municipality: {
            validators: [validators.string]
        },

        /**
         * Sets or returns the value of the `municipalitySubdivision` option.
         *
         * Represents the municipality subdivision (sub/super city) for the structured address.
         *
         * @attribute municipalitySubdivision
         * @param {String} [municipalitySubdivision] Municipality subdivision (sub/super city).
         * @throws {TypeError} If the given argument cannot be converted into a valid value.
         */
        municipalitySubdivision: {
            validators: [validators.string]
        },

        /**
         * Sets or returns the value of the `countryTertiarySubdivision` option.
         *
         * Represents the area's name for the structured address.
         *
         * @attribute countryTertiarySubdivision
         * @param {String} [countryTertiarySubdivision] Name of the area.
         * @throws {TypeError} If the given argument cannot be converted into a valid value.
         */
        countryTertiarySubdivision: {
            validators: [validators.string]
        },

        /**
         * Sets or returns the value of the `countrySecondarySubdivision` option.
         *
         * Represents the county's name for the structured address.
         *
         * @attribute countrySecondarySubdivision
         * @param {String} [countrySecondarySubdivision] County's name.
         * @throws {TypeError} If the given argument cannot be converted into a valid value.
         */
        countrySecondarySubdivision: {
            validators: [validators.string]
        },

        /**
         * Sets or returns the value of the `countrySubdivision` option.
         *
         * Represents the state or province's name for the structured address.
         *
         * @attribute countrySubdivision
         * @param {String} [countrySubdivision] State or province.
         * @throws {TypeError} If the given argument cannot be converted into a valid value.
         */
        countrySubdivision: {
            validators: [validators.string]
        },

        /**
         * Sets or returns the value of the `postalCode` option.
         *
         * Represents the zip code or postal code for the structured address.
         *
         * @attribute postalCode
         * @param {String} [postalCode] Zip or postal code.
         * @throws {TypeError} If the given argument cannot be converted into a valid value.
         */
        postalCode: {
            validators: [validators.string]
        },

        protocol: {
            validators: [validators.oneOfValue(['http', 'https'], 'protocol')]
        },
        bestResult: {
            validators: [validators.bool]
        }
    };
};



