/* eslint-disable max-len */
/**
 * Reverse geocode service implementation.
 *
 * It converts geographical coordinates into a textual address representation using the TomTom
 * [Search API - Reverse Geocode](REVERSE_GEOCODE_URL).
 *
 * Parameters need to be passed to the constructor.
 * The method {{#crossLink "Services.services.reverseGeocode/go:method"}}{{/crossLink}} performs the actual call.
 *
 * The SDK provides a {{#crossLink "Promise"}}{{/crossLink}} polyfill for browsers (IE<9)
 * without native ECMAScript 6 support.
 *
 * The final response from the service is a JSON object with the following structure:
 *
 * ```js
 * {
 *   address: {
 *     buildingNumber: 19,
 *     streetNumber: 19,
 *     landmark: '',
 *     street: 'N 2nd St',
 *     streetName: 'N 2nd St',
 *     streetNameAndNumber: '',
 *     speedLimit: '',
 *     countryCode: 'U',
 *     countrySubdivision: 'CA',
 *     countrySecondarySubdivision: 'Santa Clara',
 *     countryTertiarySubdivision: '',
 *     municipality: 'San Jose',
 *     postalCode: '95113',
 *     municipalitySubdivision: 'San Francisco Bay Area',
 *     freeformAddress: '19 N 2nd St, San Jose, CA, 95113'
 *   },
 *   position: '37.337083, -121.8898',
 *   roadUse: ['LocalStreet']
 * }
 * ```
 *
 * @example
 * ```js
 * function callbackFn(response) {
 *   console.log(response);
 * }
 * tt.services.reverseGeocode({
 *   key: <Your API key>,
 *   position: '19.449: 51.759'
 * })
 * .go()
 * .then(callbackFn);
 * ```
 *
 * @class reverseGeocode
 * @namespace Services.services
 * @module Services
 * @uses GoMixin
 * @uses KeyMixin
 * @uses ProtocolMixin
 * @uses RadiusMixin
 * @uses PositionMixin
 * @uses HeadingMixin
 * @uses LanguageMixin
 * @uses ViewMixin
 * @uses ReturnMatchTypeMixin
 * @uses AllowFreeformNewlineMixin
 * @uses BatchMixin
 * @uses EntityTypeMixin
 *
 * @constructor
 * @param {Object} [options] Options to be passed to the reverseGeocode call,
 *     or an array of such options objects to perform a batch request.
 */
/* eslint-enable */

/**
 * Sets or gets the value of the option `returnSpeedLimit`.
 *
 * Allows, if possible, the receiving of speed limit information at the given location.
 *
 * @attribute returnSpeedLimit
 * @param {Boolean} [returnSpeedLimit] Enable or disable the feature.
 * @throws {TypeError} If the given argument cannot be converted into a valid value.
 */

/**
 * Sets or gets the value of the option `number`.
 *
 * If a street number is sent in along with the request, the response may
 * include the side of the street (Left/Right), and also an offset position for that street number.
 *
 * @attribute number
 * @param {String} [number] Street number as a string.
 * @throws {TypeError} If the given argument cannot be converted into a valid value.
 */

/**
 * Sets or gets the value of the option `returnRoadUse`.
 *
 * Requires including a road use array for reversegeocodes at street level.
 *
 * @attribute returnRoadUse
 * @param {Boolean} [returnRoadUse] Enable or disable the feature.
 * @throws {TypeError} If the given argument cannot be converted into a valid value.
 */

/**
 * Sets or gets the value of the option `roadUse`.
 *
 * Use this option if you want to restrict the result to one, or a group of the following road uses:
 * - `"LimitedAccess"`
 * - `"Arterial"`
 * - `"Terminal"`
 * - `"Ramp"`
 * - `"Rotary"`
 * - `"LocalStreet"`
 *
 * @attribute roadUse
 * @param {String[]|String} [roadUse] An array of strings, or just one string with comma-separated values.
 * @throws {TypeError} If the given argument cannot be converted into a valid value.
 */

/**
 * Sets or returns the value of the `streetNumber` option.
 *
 * Represents the street's number for the structured address.
 *
 * @attribute streetNumber
 * @param {String} [streetNumber] The street's number.
 * @throws {TypeError} If the given argument cannot be converted into a valid value.
 */

import reverseGeocodeBase from './reverseGeocodeBase';
import reverseGeocodeParams from './reverseGeocodeParameters';
import types from './types';

const ReverseGeocodeBasePrototype = reverseGeocodeBase.prototype.constructor;

function ReverseGeocode(options) {
    ReverseGeocodeBasePrototype.call(this, options);
    this.fields = reverseGeocodeParams(types.reverseGeocode);
}

ReverseGeocode.prototype = new ReverseGeocodeBasePrototype();
ReverseGeocode.prototype.constructor = ReverseGeocode;

export const reverseGeocode = (options) => {
    return (new ReverseGeocode(options)).construct('reverseGeocode');
};




